import React, { useState } from 'react';
import { Form, Button } from 'react-bootstrap';
import '../../App.css';
import { Radio, RadioGroup } from '@material-ui/core';
import Axios from 'axios';
import AuthService from '../AuthService'
import withAuth__Admin from '../withAuth__Admin';

const Auth = new AuthService();

const SERVER_URL = "http://localhost:4000";


function AddRestaurants(props) {

    const Emoji = props => (
        <span
            className="star"
            role="img"
            aria-label={props.label ? props.label : ""}
            aria-hidden={props.label ? "false" : "true"}
        >
            {props.symbol}
        </span>
    )

    const handleSubmit = (evt) => {
        evt.preventDefault();
        console.log("Title: " + title); //droebit
        console.log("Category: " + cat);
        console.log("Stars: " + stars);
        console.log("Description: " + desc);
        console.log("Publish? " + publ);
        console.log("Primary?" + featuredPrimary);
        console.log("Secondary?" + featuredSecondary);
        console.log(typeof stars);
        console.log(Auth.getEmail(Auth.getToken()));

        const newRestaurant = {
            title: title,
            category: cat,
            created_by: Auth.getEmail(Auth.getToken()),
            stars: stars,
            description: desc,
            publish_immediately: publ,
            is_featured_primary: featuredPrimary,
            is_featured_secondary: featuredSecondary,
        };

        Axios.post(SERVER_URL + '/admin/restaurants/add-restaurant', newRestaurant, {
            'headers': {
                'Authorization': 'Bearer ' + Auth.getToken()
            }
        })
            .then(res => console.log(res.data))
            .catch(err => console.log(err));
    }

    const [title, setTitle] = useState("");
    const [cat, setCat] = useState("Choose one:");
    const [stars, setStars] = useState("0");
    const [desc, setDesc] = useState("");
    const [publ, setPubl] = useState(false);
    const [featuredPrimary, setFeaturedPrimary] = useState(false);
    const [featuredSecondary, setFeaturedSecondary] = useState(false);

    return (
        <div>
            <div className="container">
                <div className="col-md-5 col-md-offset-5">
                    <Form className="form" onSubmit={handleSubmit}>
                        <h1 style={{ color: 'white' }}>
                            Add a new restaurant
                    </h1>
                        <Form.Group controlId="TitleInput">
                            <Form.Label>Title of the restaurant</Form.Label>
                            <Form.Control type="text" required onChange={(e) => setTitle(e.target.value)} placeholder="Title" />
                        </Form.Group>
                        <Form.Group controlId="CategorySelect">
                            <Form.Label>Category</Form.Label>
                            <Form.Control as="select" defaultValue="Choose one" required onChange={(e) => setCat(e.target.value)}>
                                <option disabled>Choose one</option>
                                <option>Fine dining</option>
                                <option>Casual dining</option>
                                <option>Fast food</option>
                                <option>Family style</option>
                                <option>Café</option>
                            </Form.Control>
                        </Form.Group>
                        <Form.Group controlId="StarsCountSelect">
                            <Form.Label><i className="material-icons">star</i> Initial stars count</Form.Label>
                            {/* <Form.Control as="select" onChange={(e) => setStars(e.target.value)} multiple style={{height:150}}> */}
                            <br />
                            <RadioGroup aria-label="stars" required name="stars" value={stars} onChange={(e) => setStars(e.target.value)}>
                                <label><Radio value="1" required /><Emoji label="1-star" name="stars" symbol="⭐" value="1" /></label>
                                <label><Radio value="2" required></Radio><Emoji label="2-stars" symbol="⭐⭐" value="2" /></label>
                                <label><Radio value="3" required></Radio><Emoji label="3-stars" symbol="⭐⭐⭐" value="3" /></label>
                                <label><Radio value="4" required></Radio><Emoji label="4-stars" symbol="⭐⭐⭐⭐" value="4" /></label>
                                <label><Radio value="5" required></Radio><Emoji label="5-stars" symbol="⭐⭐⭐⭐⭐" value="5" /></label>
                            </RadioGroup>
                            {/* <Emoji label="2-stars" symbol="⭐⭐" value="2" />
                            <Emoji label="3-stars" symbol="⭐⭐⭐" value="3" />
                            <Emoji label="4-stars" symbol="⭐⭐⭐⭐" value="4" />
                            <Emoji label="5-stars" symbol="⭐⭐⭐⭐⭐" value="5" /> */}
                            {/* </Form.Control> */}
                        </Form.Group>
                        <Form.Group controlId="DescriptionTextarea">
                            <Form.Label>Restaurant description</Form.Label>
                            <Form.Control as="textarea" required onChange={(e) => setDesc(e.target.value)} rows="5" />
                        </Form.Group>
                        <Form.Group controlId="IsFeaturedPrimaryCheck">
                        <div class="custom-control custom-checkbox">
                            <input type="checkbox" class="custom-control-input" onChange={(e) => setFeaturedPrimary(e.target.checked)} id="IsFeaturedPrimary" />
                            <label class="custom-control-label" for="IsFeaturedPrimary">Featured Primary?</label>
                        </div>
                        </Form.Group>
                        <Form.Group controlId="IsFeaturedSecondaryCheck">
                        <div class="custom-control custom-checkbox">
                            <input type="checkbox" class="custom-control-input" onChange={(e) => setFeaturedSecondary(e.target.checked)} id="IsFeaturedSecondary" />
                            <label class="custom-control-label" for="IsFeaturedSecondary">Featured Secondary?</label>
                        </div>
                        </Form.Group>
                        <Form.Group controlId="PublishImmediatelyCheck">
                        <div class="custom-control custom-checkbox">
                            <input type="checkbox" class="custom-control-input" onChange={(e) => setPubl(e.target.checked)} id="PublishImmediately" />
                            <label class="custom-control-label" for="PublishImmediately">Publish Immediately?</label>
                        </div>
                        </Form.Group>
                        <Button type="submit" value="Register">Add restaurant</Button>
                    </Form>
                </div>
            </div>
        </div>
    )

}

export default withAuth__Admin(AddRestaurants);