import React from 'react';
import './App.css';
import Home from './components/home/home.component';
import "bootstrap/dist/css/bootstrap.min.css";
import { Route, Switch } from 'react-router-dom';
import SignUp from './components/signup/signup.component';
import NavBar from './components/navbar/navbar.component';
import Prices from './components/prices/prices.component';
import Calculator from './components/calculator/calculator.component';
import ListUsers from './components/admin/list-users.component';
import EditUser from './components/admin/edit-user.component';
import SignInForm from './components/signin/signin.component';
import AddRestaurants from './components/restaurants/add-restaurants.component';
import SignInSignUpModal from './components/signin-signup/only-login'
import SignInSignUpModal2 from './components/signin-signup/signin-signup.component2';
import NotFound from './components/not-found/not-found.component';
import AllRestaurants from './components/restaurants/all-restaurants.component';
// import Login from './components/Login';

function App() {
  return (
    <div className="App-container">
      <NavBar />
      <Switch>
      <Route exact={true} path={'/'} component={Home} />
      <Route path={'/register'} component={SignUp} />
      <Route path={'/prices'} component={Prices} />
      <Route path={'/calculator'} component={Calculator} />
      <Route path={'/list-users'} component={ListUsers} />
      <Route path={'/users/:id'} component={EditUser} />
      <Route path={'/login'} component={SignInForm} />
      <Route path={'/add-restaurant'} component={AddRestaurants} />
      <Route path={'/modal'} component={SignInSignUpModal} />
      <Route path={'/modal2'} component={SignInSignUpModal2} />
      <Route path={'/all-restaurants'} component={AllRestaurants} />
      <Route component={NotFound} />
      </Switch>
    </div>
  );
}

export default App;